#flash {
  text-align:center;
  overflow:hidden;
  height:0;
  padding:0;
  -webkit-animation: showAnimation 0.25s ease-out 0s forwards, hideAnimation 0.5s ease-in 3s forwards;
          animation: showAnimation 0.25s ease-out 0s forwards, hideAnimation 0.5s ease-in 3s forwards;
}

#flash.success {
  background:rgb(0,255,0);
}

#flash.warn {
  background:orange;
}

#flash.error {
  background:red;
}


@-webkit-keyframes hideAnimation {
  0% {
    height:1.5em;
    padding:1em;
    opacity:1;
  }
  100% {
    height:0;
    padding:0;
    opacity:0;
  }
}


@keyframes hideAnimation {
  0% {
    height:1.5em;
    padding:1em;
    opacity:1;
  }
  100% {
    height:0;
    padding:0;
    opacity:0;
  }
}

@-webkit-keyframes showAnimation {
  0% {
    height:0;
    padding:0;
    opacity:0;
  }
  100% {
    height:1.5em;
    padding:1em;
    opacity:1;
  }
}

@keyframes showAnimation {
  0% {
    height:0;
    padding:0;
    opacity:0;
  }
  100% {
    height:1.5em;
    padding:1em;
    opacity:1;
  }
}


